import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import GUI from 'lil-gui'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader.js'

import model from './models/IbernianShaman.glb?url'

import floorDiff from '/textures/wood_cabinet_worn_long/wood_cabinet_worn_long_diff_1k.jpg?url'
import floorNorm from '/textures/wood_cabinet_worn_long/wood_cabinet_worn_long_nor_gl_1k.png?url'
import floorAORoughMetal from '/textures/wood_cabinet_worn_long/wood_cabinet_worn_long_arm_1k.jpg?url'

import wallDiff from '/textures/castle_brick_broken_06/castle_brick_broken_06_diff_1k.jpg?url'
import wallNorm from '/textures/castle_brick_broken_06/castle_brick_broken_06_nor_gl_1k.png?url'
import wallAORoughMetal from '/textures/castle_brick_broken_06/castle_brick_broken_06_arm_1k.jpg?url'

import environ from '/environmentMaps/0/2k.hdr?url'

/**
 * Loaders
 */
const gltfLoader = new GLTFLoader()
const rgbeLoader = new RGBELoader()
const textureLoader = new THREE.TextureLoader()

/**
 * Base
 */
// Debug
const gui = new GUI()
const global = {}

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()

/**
 * Update all materials
 */
const updateAllMaterials = () =>
{
    scene.traverse((child) =>
    {
        if(child.isMesh && child.material.isMeshStandardMaterial)
        {
            child.material.envMapIntensity = global.envMapIntensity
            child.castShadow = true
            child.receiveShadow = true
        }
    })
}


/**
 * Models
 */
// Helmet
// gltfLoader.load(
//     '/models/FlightHelmet/glTF/FlightHelmet.gltf',
//     (gltf) =>
//     {
//         gltf.scene.scale.set(10, 10, 10)
//         scene.add(gltf.scene)

//         updateAllMaterials()
//     }
// )

// // Custom model
// gltfLoader.load(
//     '/models/IbernianShaman.glb',
//     (gltf) =>
//     {
//         gltf.scene.scale.set(.05,.05,.05)
//         scene.add(gltf.scene)

//         updateAllMaterials()
//     }
// )

// Floor
const floorColorTexture = textureLoader.load(floorDiff)
const floorNormalTexture = textureLoader.load(floorNorm)
//const floorAORoughnessMetalnessTexture = textureLoader.load(floorAORoughMetal)
floorColorTexture.colorSpace = THREE.SRGBColorSpace
const floorMat = new THREE.MeshStandardMaterial({
        map: floorColorTexture,
        normalMap: floorNormalTexture,
    })

const floor = new THREE.Mesh(
    new THREE.PlaneGeometry(8,8),
    floorMat
)
floor.receiveShadow = true
floor.rotation.x = -Math.PI*.5
scene.add(floor)

// Wall
const wallColorTexture = textureLoader.load(wallDiff)
const wallNormalTexture = textureLoader.load(wallNorm)
//const wallAORoughnessMetalnessTexture = textureLoader.load(wallAORoughMetal)
wallColorTexture.colorSpace = THREE.SRGBColorSpace
const wallMat = new THREE.MeshStandardMaterial({
    map: wallColorTexture,
    normalMap: wallNormalTexture,
})
const wall = new THREE.Mesh(
    new THREE.PlaneGeometry(8,8),
    wallMat
)
wall.receiveShadow = true
wall.position.y = 4
wall.position.z = -4
scene.add(wall)

updateAllMaterials()

gltfLoader.load(
    model,
    (gltf) =>
    {
        //console.log(gltf)
        gltf.scene.traverse((obj) => {
            if(obj.isMesh){
                const oldMat = obj.material
                obj.material = new THREE.MeshStandardMaterial({
                    color:oldMat.color,
                    map:oldMat.map,
                    bumpMap:oldMat.bumpMap,
                    displacementMap:oldMat.displacementMap,
                    alphaMap:oldMat.alphaMap,
                    normalMap:oldMat.normalMap,
                    aoMap:oldMat.aoMap
                })
            }
        })
        for(let i=0; i < 6; i++){
            gltf.scene.children[0].children[0].children[5].children[i].material.side = THREE.DoubleSide
        }
        
        gltf.scene.scale.set(.05,.05,.05)
        gltf.scene.position.set(0,-.1,0)
        scene.add(gltf.scene)
        updateAllMaterials()
    }
)

/**
 * Environment map
 */
// Global intensity
global.envMapIntensity = 1
gui
    .add(global, 'envMapIntensity')
    .min(0)
    .max(10)
    .step(0.001)
    .onChange(updateAllMaterials)

// HDR (RGBE) equirectangular
rgbeLoader.load(environ, (environmentMap) =>
{
    environmentMap.mapping = THREE.EquirectangularReflectionMapping

    scene.background = environmentMap
    scene.environment = environmentMap
})

// Ambient Light
const ambientLight = new THREE.AmbientLight(0xffffff, 1)
gui.add(ambientLight, 'intensity').min(0).max(1).step(0.001)
scene.add(ambientLight)

/**
 * Directional Light
 */
const directionalLight = new THREE.DirectionalLight('#ffffff', 6)
directionalLight.position.set(-4,6.5,8.5)
directionalLight.scale.set(2,2,2)
scene.add(directionalLight)

gui.add(directionalLight, 'intensity',0,10,0.001).name('lightIntensity')
gui.add(directionalLight.position, 'x',-10,10,0.001).name('lightX')
gui.add(directionalLight.position, 'y',-10,10,0.001).name('lightY')
gui.add(directionalLight.position, 'z',-10,10,0.001).name('lightZ')

// Shadows
directionalLight.castShadow = true
directionalLight.shadow.camera.far =25
directionalLight.shadow.mapSize.set(512,512)
directionalLight.shadow.camera.left = -10
directionalLight.shadow.camera.bottom = -10
directionalLight.shadow.camera.right = 10
directionalLight.shadow.camera.top = 10
gui.add(directionalLight,'castShadow')
gui.add(directionalLight.shadow,'normalBias',-.05,.05,0.001)
gui.add(directionalLight.shadow,'bias',-.05,.05,0.001)

// Target
directionalLight.target.position.set(0,2,0)
directionalLight.target.updateWorldMatrix()

// // Helper
// const directionalLightHelper = new THREE.DirectionalLightHelper(directionalLight)
// scene.add(directionalLightHelper)
// const directionalLightShadowHelper = new THREE.CameraHelper(directionalLight.shadow.camera)
// scene.add(directionalLightShadowHelper)

// directionalLightHelper.update()

scene.add(directionalLight.target)

/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
camera.position.set(4, 5, 4)
scene.add(camera)

// Controls
const controls = new OrbitControls(camera, canvas)
controls.target.y = 3.5
controls.enableDamping = true

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias:true
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

// Tone mapping
renderer.toneMapping = THREE.ReinhardToneMapping
renderer.toneMappingExposure = 2
gui.add(renderer,'toneMapping', {
    No: THREE.NoToneMapping,
    Linear: THREE.LinearToneMapping,
    Reinhard: THREE.ReinhardToneMapping,
    Cineon: THREE.CineonToneMapping,
    ACESFilmic:THREE.ACESFilmicToneMapping
})
gui.add(renderer,'toneMappingExposure',0,10,0.001) // min, max, step

// Physically accurate lighting
//renderer.useLegacyLights = false
//gui.add(renderer, 'useLegacyLights')

// Shadows
renderer.shadowMap.enabled = true
renderer.shadowMap.type = THREE.PCFSoftShadowMap

/**
 * Animate
 */
const tick = () =>
{
    // Update controls
    controls.update()

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()